import { Component, h, Element, Prop, Watch } from '@stencil/core';
import * as d3 from 'd3';


interface GradeType
{
    name: string,
    semester1:number,
    semester2:number
}

type HistogramBin = d3.Bin<number, number>;//for the Bins


const studentGrades :GradeType[]= [
  { name: "Student 1", semester1: 78, semester2: 82 },
  { name: "Student 2", semester1: 85, semester2: 88 },
  { name: "Student 3", semester1: 92, semester2: 95 },
  { name: "Student 4", semester1: 64, semester2: 70 },
  { name: "Student 5", semester1: 71, semester2: 75 },
  { name: "Student 6", semester1: 88, semester2: 91 },
  { name: "Student 7", semester1: 53, semester2: 62 },
  { name: "Student 8", semester1: 79, semester2: 83 },
  { name: "Student 9", semester1: 95, semester2: 97 },
  { name: "Student 10", semester1: 68, semester2: 73 },
  { name: "Student 11", semester1: 82, semester2: 85 },
  { name: "Student 12", semester1: 76, semester2: 80 },
  { name: "Student 13", semester1: 89, semester2: 92 },
  { name: "Student 14", semester1: 61, semester2: 68 },
  { name: "Student 15", semester1: 73, semester2: 77 },
  { name: "Student 16", semester1: 84, semester2: 87 },
  { name: "Student 17", semester1: 57, semester2: 65 },
  { name: "Student 18", semester1: 91, semester2: 94 },
  { name: "Student 19", semester1: 67, semester2: 72 },
  { name: "Student 20", semester1: 80, semester2: 84 },
  { name: "Student 21", semester1: 74, semester2: 79 },
  { name: "Student 22", semester1: 83, semester2: 86 },
  { name: "Student 23", semester1: 69, semester2: 74 },
  { name: "Student 24", semester1: 90, semester2: 93 },
  { name: "Student 25", semester1: 58, semester2: 66 },
  { name: "Student 26", semester1: 77, semester2: 81 },
  { name: "Student 27", semester1: 86, semester2: 89 },
  { name: "Student 28", semester1: 63, semester2: 69 },
  { name: "Student 29", semester1: 94, semester2: 96 },
  { name: "Student 30", semester1: 72, semester2: 76 },
];

@Component({
    tag:"histogram-chart",
    styleUrl:"histogram-chart.css",
    shadow:true
})
export class HistogramChart
{
    @Element() el! : HTMLElement;
    @Prop() myWidth:number =800
    @Prop() myHight:number=500
    @Prop() nameOfChart:string="Grade in 1st Semester vs 2nd Semester"
    @Prop() binCount:number=20

    @Prop() data: GradeType[] = []

    private resizeObserver!: ResizeObserver;//resizesing

    renderHistogram()
    {
        const isMobile = this.myWidth < 500;
            const margin = {
            top: isMobile ? 50 : 70,
            right: 40,
            bottom: isMobile ? 50 : 60,
            left: isMobile ? 60 : 175
            };
        const width = this.myWidth - margin.left - margin.right;
        const legendPadding = isMobile ? 60 : 0;
        const height = this.myHight - margin.top - margin.bottom - legendPadding;

        const container = this.el.shadowRoot?.querySelector('.histogramChart');
        if (container) {
            container.innerHTML = '';//remove the repreted SVG
        }
        const svg = d3.select(this.el.shadowRoot?.querySelector('.histogramChart')!)
                    .append('svg')
                    .attr("viewBox", `0 0 ${this.myWidth} ${this.myHight + (isMobile ? 60 : 0)}`)
                    .attr("preserveAspectRatio", "xMidYMid meet")
                    .append('g')
                    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

        const allGrade :number[] = [...this.data.map(d=>d.semester1) , ...this.data.map(d=>d.semester2 )]
                                                                                        .filter((grade): grade is number=> grade !== undefined && grade !== null);

        // X Sacle
        const x= d3.scaleLinear<number,number>()
                        .domain([0, d3.max(allGrade)||0])
                        .range([0,width])
                        .nice()

        const bins = d3.bin()
            .value(d=>d)
            .domain(x.domain() as [number,number])// Ensure domain is [min, max] (and the return from xScale is number[])
            .thresholds(x.ticks(this.binCount)); // Divides the domain into `binCount` (e.g., 10) equal intervals

        const semester1Bins = bins(this.data.map(d => d.semester1));
        const semester2Bins = bins(this.data.map(d => d.semester2));

        // Y scale (frequency)
        const y = d3.scaleLinear()
            .domain([0, d3.max([...semester1Bins, ...semester2Bins], d => d.length)||0])
            .range([height, 0])
            .nice();

        // Color scale
         const color = d3.scaleOrdinal<string, string>()
            .domain(["semester1", "semester2"])
            .range(["#1f77b4", "#ff7f0e"])

            const getBinX = (d: HistogramBin) => x(d.x0 ?? 0);
            const getBinWidth = (d: HistogramBin) => x(d.x1 ?? 0) - x(d.x0 ?? 0);
            const barWidth = (d: HistogramBin) => getBinWidth(d) / 2;

        // Draw semester 1 histogram
        svg.selectAll<SVGRectElement, HistogramBin>(".semester1")
            .data(semester1Bins)
            .enter().append("rect")
            .attr("class", "bar-s1")
            .attr("y", d => y(d.length ?? 0)) // Y position of the bar (top of the bar based on frequency)
            .attr("x", d => getBinX(d) + getBinWidth(d) / 4 - barWidth(d) / 2)
            .attr("width", d => barWidth(d))
            .attr("height", d => height - y(d.length ?? 0))// Height of the bar
            .attr("fill", () => color("semester1") as string)
            .attr("rx", 5) // Rounded corners (horizontal)
            .attr("ry", 5) // Rounded corners (vertical)
            .attr("opacity", 0.7) // Make bars slightly transparent for overlay effect
            .on("mouseover", function () {
                d3.select(this).attr("opacity", 1);
            })
            .on("mouseout", function () {
                d3.select(this).attr("opacity", 0.7);
            })



            // Draw semester 2 histogram (overlaid)
            svg.selectAll<SVGRectElement, HistogramBin>(".semester2")
                .data(semester2Bins)
                .enter().append("rect")
                .attr("class", "bar-s2")
                .attr("y", d => y(d.length ?? 0))
                .attr("x", d => getBinX(d) + getBinWidth(d) / 4 + barWidth(d) / 2)
                .attr("width", d => barWidth(d))
                .attr("height", d => height - y(d.length ?? 0))
                .attr("fill", () => color("semester2") as string)
                .attr("rx", 5) // Rounded corners (horizontal)
                .attr("ry", 5) // Rounded corners (vertical)
                .attr("opacity", 0.7) // Make bars slightly transparent for overlay effect
                .on("mouseover", function () {
                    d3.select(this).attr("opacity", 1);
                })
                .on("mouseout", function () {
                    d3.select(this).attr("opacity", 0.7);
                })

            // Add X axis
            const xAxisGroup = svg.append("g")
            .attr("transform", `translate(0,${height})`)
            .call(d3.axisBottom(x).ticks(this.binCount));

            xAxisGroup.append("text")
            .attr("x", width / 2)
            .attr("y", 35)
            .attr("fill", "var(--theme-color-std-text)")
            .style("text-anchor", "middle")
            .text("Grade Ranges");

            xAxisGroup.selectAll("text")
            .style("font-size", isMobile ? "10px" : "12px");

            // Add Y axis
            svg.append("g")
            .call(d3.axisLeft(y))
            .append("text")
            .attr("transform", "rotate(-90)")
            .attr("y", -40)
            .attr("x", -height / 2)
            .attr("fill", "var(--theme-color-std-text)")
            .text("Number of Students");

            const titleFontSize = isMobile ? "16px" : "20px";

            svg.append("text")
            .attr("class", "chart-title")
            .attr("x", width / 2)
            .attr("y", -20)
            .style("text-anchor", "middle")
            .style("font-size", titleFontSize)
            .text(this.nameOfChart);

            // Add legend
            const legendX = isMobile ? 10 : width - 100;
            const legendY = isMobile ? height + 40 : -50;

            const legend = svg.append("g")
            .attr("transform", `translate(${legendX}, ${legendY})`);

            legend.append("rect")
            .attr("x", 0)
            .attr("width", 18)
            .attr("height", 18)
            .attr("fill", color("semester1"))
            .attr("opacity", 0.6);

            legend.append("text")
            .attr("x", 25)
            .attr("y", 9)
            .attr("dy", "0.35em")
            .text("Semester 1")
            .attr("fill", "var(--theme-color-std-text)")

            legend.append("rect")
            .attr("x", 0)
            .attr("y", 25)
            .attr("width", 18)
            .attr("height", 18)
            .attr("fill", color("semester2"))
            .attr("opacity", 0.6);

            legend.append("text")
            .attr("x", 25)
            .attr("y", 34)
            .attr("dy", "0.35em")
            .text("Semester 2")
            .attr("fill", "var(--theme-color-std-text)")

            legend.selectAll("rect")
            .on("mouseover", (event, d) => {
                const semester = event.target?.nextSibling?.textContent?.includes("1") ? "bar-s1" : "bar-s2";
                svg.selectAll<SVGRectElement, HistogramBin>(`.${semester}`).attr("opacity", 1);
            })
            .on("mouseout", () => {
                svg.selectAll(".bar-s1").attr("opacity", 0.7);
                svg.selectAll(".bar-s2").attr("opacity", 0.7);
            });

    }

    @Watch('myWidth')
    @Watch('myHeight')
    @Watch('nameOfChart')
    @Watch('binCount')
    @Watch("data")
    OnPropChange() {
        this.renderHistogram();
    }

    observeResize() {
    this.resizeObserver = new ResizeObserver(entries => {
    for (const entry of entries) {
        const { width, height } = entry.contentRect;
        if (Math.abs(this.myWidth - width) > 1 || Math.abs(this.myHight - height) > 1) {
            this.myWidth = width;
            this.myHight = height;
            this.renderHistogram();
            }
        }
    });
    const container = this.el.shadowRoot?.querySelector('.histogramChart')!;
        if (container) {
        this.resizeObserver.observe(container);
     }
    }

    componentDidLoad() {
    this.renderHistogram();
    this.observeResize();
    }

    disconnectedCallback() {
    if (this.resizeObserver) {
        this.resizeObserver.disconnect();
    }
    }
    render()
    {
        return(
          <div>
            <ix-style-loader></ix-style-loader>

            <div class="histogramChart">

            </div>
          </div>
        )
    }
}
